
@mixin flexproperty($alignItem, $justifyContent, $flexDirection) {
  display: flex;
  align-items: $alignItem;
  justify-content: $justifyContent;
  flex-direction: $flexDirection;
}

.flex {
  @include flexproperty($alignItem:center, $justifyContent:center, $flexDirection:column);
}

@mixin boxShadow($color) {
  box-shadow: 
  0px 24px 51px -5px ($color), 
  0px 56px 101px -3px ($color), 
  3px 84px 87px 1px ($color), 
  -2px 110px 105px 10px ($color), 
  -48px 202px 126px 10px ($color), 
  9px 316px 89px 3px ($color);
}


@mixin underline($width) {
  content: '';
  position: absolute;
  width: $width;
  height: 2px;
  background-color: var(--secondary-color);
  bottom: 0;
  left: 0;
  transition: .6s;
}